import axios from 'axios';

export default class MachineServicesApi{

    static list(token, params){
        return axios({
            method: "GET",
            headers: {"Authorization": `Bearer ${token}`},
            params,
            url: "/api/browser/settings/services"
        });
    }

    static add(token, data){
        return axios({
            method: "POST",
            headers: {"Authorization": `Bearer ${token}`},
            data,
            url: "/api/browser/settings/services"
        });
    }

    static edit(token, service, data){
        return axios({
            method: "PUT",
            headers: {"Authorization": `Bearer ${token}`},
            data,
            url: `/api/browser/settings/services/${service}`
        });
    }

    static delete(token, service){
        return axios({
            method: "DELETE",
            headers: {"Authorization": `Bearer ${token}`},
            url: `/api/browser/settings/services/${service}`
        });
    }
}


