import axios from 'axios';

export default class PrintersApi{

    static list(token, params){
        return axios({
            method: "GET",
            headers: {"Authorization": `Bearer ${token}`},
            params,
            url: "/api/browser/settings/printers"
        });
    }

    static add(token, data){
        return axios({
            method: "POST",
            headers: {"Authorization": `Bearer ${token}`},
            data,
            url: "/api/browser/settings/printers"
        });
    }

    static edit(token, printer, data){
        return axios({
            method: "PUT",
            headers: {"Authorization": `Bearer ${token}`},
            data,
            url: `/api/browser/settings/printers/${printer}`
        });
    }

    static delete(token, printer){
        return axios({
            method: "DELETE",
            headers: {"Authorization": `Bearer ${token}`},
            url: `/api/browser/settings/printers/${printer}`
        });
    }
}


