import * as actionTypes from '../helpers/constants';
import {getTime} from "date-fns";
import MachineApplicationsApi from '../api/actual/MachineApplications';


export const loadApplications = (token, params = {}) => dispatch => {
    dispatch({type: actionTypes.LOAD_MACHINE_APPLICATIONS});

    return MachineApplicationsApi
        .list(token, params)
        .then(({data: applications}) => {
            dispatch({type: actionTypes.LOAD_MACHINE_APPLICATIONS_SUCCESSFUL, applications});
        })
        .catch(error => {
            dispatch({type: actionTypes.LOAD_MACHINE_APPLICATIONS_FAILED});
            console.log(error);});
};

export const addApplication = (token, data) => dispatch => {
    dispatch({type: actionTypes.ADD_MACHINE_APPLICATION});

    return MachineApplicationsApi
        .add(token, data)
        .then(({data: {created}}) => {
            if(created){
                dispatch({type: actionTypes.ADD_MACHINE_APPLICATION_SUCCESSFUL});
                dispatch({
                    type: actionTypes.OPERATION_SUCCESSFUL,
                    action: actionTypes.ADD_MACHINE_APPLICATION,
                    timestamp: getTime(Date()),
                    data: {created}
                });
            }
        })
        .catch(error => {
            dispatch({
                type: actionTypes.OPERATION_FAILED,
                action: actionTypes.ADD_MACHINE_APPLICATION,
                timestamp: getTime(Date()),
                data: {...error}
            });
            dispatch({type: actionTypes.ADD_MACHINE_APPLICATION_FAILED});
            console.log(error);});
};

export const editApplication = (token, application, data) => dispatch => {
    dispatch({type: actionTypes.EDIT_MACHINE_APPLICATION});

    return MachineApplicationsApi
        .edit(token, application, data)
        .then(({data: {updated}}) => {
            if(updated){
                dispatch({type: actionTypes.EDIT_MACHINE_APPLICATION_SUCCESSFUL});

                dispatch({
                    type: actionTypes.OPERATION_SUCCESSFUL,
                    action: actionTypes.EDIT_MACHINE_APPLICATION,
                    timestamp: getTime(Date()),
                    data: {updated}
                });
            }
        })
        .catch(error => {
            dispatch({
                type: actionTypes.OPERATION_FAILED,
                action: actionTypes.EDIT_MACHINE_APPLICATION,
                timestamp: getTime(Date()),
                data: {...error}
            });
            dispatch({type: actionTypes.EDIT_MACHINE_APPLICATION_FAILED});
            console.log(error);});
};

export const deleteApplication = (token, application) => dispatch => {
    dispatch({type: actionTypes.DELETE_MACHINE_APPLICATION});

    return MachineApplicationsApi
        .delete(token, application)
        .then(({data: {deleted}}) => {
            if(deleted){
                dispatch({type: actionTypes.DELETE_MACHINE_APPLICATION_SUCCESSFUL});
                dispatch({
                    type: actionTypes.OPERATION_SUCCESSFUL,
                    action: actionTypes.DELETE_MACHINE_APPLICATION,
                    timestamp: getTime(Date()),
                    data: {deleted}
                });
            }
        })
        .catch(error => {
            dispatch({
                type: actionTypes.OPERATION_FAILED,
                action: actionTypes.DELETE_MACHINE_APPLICATION,
                timestamp: getTime(Date()),
                data: {...error}
            });
            dispatch({type: actionTypes.DELETE_MACHINE_APPLICATION_FAILED});
            console.log(error);});
};


