import * as actionTypes from '../helpers/constants';
import {getTime} from "date-fns";
import PrintersApi from '../api/actual/Printers';


export const loadPrinters = (token, params = {}) => dispatch => {
    dispatch({type: actionTypes.LOAD_PRINTERS});

    return PrintersApi
        .list(token, params)
        .then(({data: printers}) => {
            dispatch({type: actionTypes.LOAD_PRINTERS_SUCCESSFUL, printers});
        })
        .catch(error => {
            dispatch({type: actionTypes.LOAD_PRINTERS_FAILED});
            console.log(error);});
};

export const addPrinter = (token, data) => dispatch => {
    dispatch({type: actionTypes.ADD_PRINTER});

    return PrintersApi
        .add(token, data)
        .then(({data: {created}}) => {
            if(created){
                dispatch({type: actionTypes.ADD_PRINTER_SUCCESSFUL});
                dispatch({
                    type: actionTypes.OPERATION_SUCCESSFUL,
                    action: actionTypes.ADD_PRINTER,
                    timestamp: getTime(Date()),
                    data: {created}
                });
            }
        })
        .catch(error => {
            dispatch({
                type: actionTypes.OPERATION_FAILED,
                action: actionTypes.ADD_PRINTER,
                timestamp: getTime(Date()),
                data: {...error}
            });
            dispatch({type: actionTypes.ADD_PRINTER_FAILED});
            console.log(error);});
};

export const editPrinter = (token, printer, data) => dispatch => {
    dispatch({type: actionTypes.EDIT_PRINTER});

    return PrintersApi
        .edit(token, printer, data)
        .then(({data: {updated}}) => {
            if(updated){
                dispatch({type: actionTypes.EDIT_PRINTER_SUCCESSFUL});

                dispatch({
                    type: actionTypes.OPERATION_SUCCESSFUL,
                    action: actionTypes.EDIT_PRINTER,
                    timestamp: getTime(Date()),
                    data: {updated}
                });
            }
        })
        .catch(error => {
            dispatch({
                type: actionTypes.OPERATION_FAILED,
                action: actionTypes.EDIT_PRINTER,
                timestamp: getTime(Date()),
                data: {...error}
            });
            dispatch({type: actionTypes.EDIT_PRINTER_FAILED});
            console.log(error);});
};

export const deletePrinter = (token, printer) => dispatch => {
    dispatch({type: actionTypes.DELETE_PRINTER});

    return PrintersApi
        .delete(token, printer)
        .then(({data: {deleted}}) => {
            if(deleted){
                dispatch({type: actionTypes.DELETE_PRINTER_SUCCESSFUL});
                dispatch({
                    type: actionTypes.OPERATION_SUCCESSFUL,
                    action: actionTypes.DELETE_PRINTER,
                    timestamp: getTime(Date()),
                    data: {deleted}
                });
            }
        })
        .catch(error => {
            dispatch({
                type: actionTypes.OPERATION_FAILED,
                action: actionTypes.DELETE_PRINTER,
                timestamp: getTime(Date()),
                data: {...error}
            });
            dispatch({type: actionTypes.DELETE_PRINTER_FAILED});
            console.log(error);});
};


