import React from 'react';
import PropTypes from 'prop-types';
import { Card, Text, Spinner } from '@blueprintjs/core';
import {NewNetworkDialog, ViewNetworkDialog} from './Modal';
import {Route, Link} from 'react-router-dom';
import {NetworksTable} from './Table';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { bindActionCreators } from 'redux';
import { loadNetworks } from '../../../actions/NetworksActions';


class Networks extends React.Component{

    componentDidMount(){
        const {authUser: {token}, loadNetworks} = this.props;
        loadNetworks(token, {});
    }

    render(){

        return (
            <Card className="page-cell">
                <header className="Header">
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        {this.props.loadingApplications ? <Spinner className="bp3-small"/> : null }
                        <Text className="devices__text">
                            Networks <small className="bp3-text-muted">({this.props.networks.length})</small>
                        </Text>
                    </div>

                    <Link to={`/settings/networks/new`} className="bp3-button bp3-icon-plus"/>
                </header>
                <div className="Content">
                    <Route path="/settings/networks/new" component={NewNetworkDialog}/>
                    <Route path="/settings/networks/:id/view" component={ViewNetworkDialog}/>
                    
                    <NetworksTable data={this.props.networks}/>
                </div>
            </Card>
        );
    }

    static = {
        authUser: PropTypes.object.isRequired,
        networks: PropTypes.array.isRequired,
        loadingNetworks: PropTypes.bool.isRequired,
        loadNetworks: PropTypes.func.isRequired
    };
}

const mapStateToProps = ({authUser, networks, loadingNetworks}) => ({authUser, networks, loadingNetworks});
const mapDispatchToProps = dispatch => bindActionCreators({loadNetworks}, dispatch);

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Networks));


