import React from 'react';
import PropTypes from 'prop-types';
import {Card, Text, Spinner} from '@blueprintjs/core';
import {NewServiceDialog, ViewServiceDialog} from './Modal';
import {Route, Link} from 'react-router-dom';
import {ServicesTable} from './Table';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { bindActionCreators } from 'redux';
import { loadServices } from '../../../actions/MachineServicesActions';


class Services extends React.Component{

    componentDidMount(){
        const {authUser: {token}, loadServices} = this.props;
        loadServices(token, {});
    }

    render(){

        return (
            <Card className="page-cell">
                <header className="Header">
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        {this.props.loadingApplications ? <Spinner className="bp3-small"/> : null }
                        <Text className="devices__text">
                            Monitored Services <small className="bp3-text-muted">({this.props.services.length})</small>
                        </Text>
                    </div>
                    <Link to={`/settings/services/new`} className="bp3-button bp3-icon-plus"/>
                </header>
                <div className="Content">
                    <Route path="/settings/services/new" component={NewServiceDialog}/>
                    <Route path="/settings/services/:id/view" component={ViewServiceDialog}/>
                    
                    <ServicesTable data={this.props.services}/>
                </div>
            </Card>
        );
    }

    static = {
        authUser: PropTypes.object.isRequired,
        services: PropTypes.array.isRequired,
        loadingServices: PropTypes.bool.isRequired,
        loadServices: PropTypes.func.isRequired
    };
}

const mapStateToProps = ({authUser, services, loadingServices}) => ({authUser, services, loadingServices});
const mapDispatchToProps = dispatch => bindActionCreators({loadServices}, dispatch);

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Services));


