import Loadable from 'react-loadable';
import PropTypes from "prop-types";
import {Dialog} from "@blueprintjs/core";
import React from "react";
import Loading from '../../../Common/loading';

const NewPrinter = Loadable({
    loader: () => import('./New'),
    loading: () => <Loading/>,
});

const ViewPrinter = Loadable({
    loader: () => import('./View'),
    loading: () => <Loading/>,
});


export const NewPrinterDialog = ({history}) => {
    return (
        <Dialog
            backdropClassName="transparent__back"
            style={{width: '350px'}}
            lazy
            onClose={() => history.goBack()}
            hasBackdrop={false}
            canOutsideClickClose={false}
            icon="print"
            isOpen title={"New Printer"}
        >
            <NewPrinter/>
        </Dialog>
    );
};
NewPrinterDialog.propTypes = {
    history: PropTypes.object.isRequired
};

export const ViewPrinterDialog = ({history}) => {
    return (
        <Dialog
            backdropClassName="transparent__back"
            style={{width: '350px'}}
            lazy
            onClose={() => history.goBack()}
            hasBackdrop={false}
            canOutsideClickClose={false}
            icon="print"
            isOpen title={"Printer"}
        >
            <ViewPrinter/>
        </Dialog>
    );
};
ViewPrinterDialog.propTypes = {
    history: PropTypes.object.isRequired
};


