import React from 'react';
import PropTypes from 'prop-types';
import {Card, Text, Spinner, Icon} from '@blueprintjs/core';
import {NewApplicationDialog, ViewApplicationDialog} from './Modal';
import {Route, Link} from 'react-router-dom';
import {ApplicationsTable} from './Table';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { bindActionCreators } from 'redux';
import { loadApplications } from '../../../actions/MachineApplicationsActions';


class Applications extends React.Component{

    componentDidMount(){
        const {authUser: {token}, loadApplications} = this.props;
        loadApplications(token, {});
    }

    render(){

        return (
            <Card className="page-cell">
                <header className="Header">
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        {this.props.loadingApplications ? <Spinner className="bp3-small"/> : null }
                        <Text className="devices__text">
                            Monitored Applications <small className="bp3-text-muted">({this.props.applications.length})</small>
                        </Text>
                    </div>
                    <Link to={`/settings/applications/new`} className="bp3-button bp3-icon-plus"/>
                </header>
                <div className="Content">
                    <Route path="/settings/applications/new" component={NewApplicationDialog}/>
                    <Route path="/settings/applications/:id/view" component={ViewApplicationDialog}/>
                    
                    <ApplicationsTable data={this.props.applications}/>
                </div>
            </Card>
        );
    }

    static = {
        authUser: PropTypes.object.isRequired,
        applications: PropTypes.array.isRequired,
        loadingApplications: PropTypes.bool.isRequired,
        loadApplications: PropTypes.func.isRequired
    };
}

const mapStateToProps = ({authUser, applications, loadingApplications}) => ({authUser, applications, loadingApplications});
const mapDispatchToProps = dispatch => bindActionCreators({loadApplications}, dispatch);

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Applications));


