import Loadable from 'react-loadable';
import PropTypes from "prop-types";
import {Dialog} from "@blueprintjs/core";
import React from "react";
import Loading from '../../../Common/loading';

const NewNetwork = Loadable({
    loader: () => import('./New'),
    loading: () => <Loading/>,
});

const ViewNetwork = Loadable({
    loader: () => import('./View'),
    loading: () => <Loading/>,
});


export const NewNetworkDialog = ({history}) => {
    return (
        <Dialog
            backdropClassName="transparent__back"
            style={{width: '350px'}}
            lazy
            onClose={() => history.goBack()}
            hasBackdrop={false}
            canOutsideClickClose={false}
            icon="cell-tower"
            isOpen title={"New Network"}
        >
            <NewNetwork/>
        </Dialog>
    );
};
NewNetworkDialog.propTypes = {
    history: PropTypes.object.isRequired
};

export const ViewNetworkDialog = ({history}) => {
    return (
        <Dialog
            backdropClassName="transparent__back"
            style={{width: '350px'}}
            lazy
            onClose={() => history.goBack()}
            hasBackdrop={false}
            canOutsideClickClose={false}
            icon="cell-tower"
            isOpen title={"Network"}
        >
            <ViewNetwork/>
        </Dialog>
    );
};
ViewNetworkDialog.propTypes = {
    history: PropTypes.object.isRequired
};


