import Loadable from 'react-loadable';
import PropTypes from "prop-types";
import {Dialog} from "@blueprintjs/core";
import React from "react";
import Loading from '../../../Common/loading';

const NewService = Loadable({
    loader: () => import('./New'),
    loading: () => <Loading/>,
});

const ViewService = Loadable({
    loader: () => import('./View'),
    loading: () => <Loading/>,
});


export const NewServiceDialog = ({history}) => {
    return (
        <Dialog
            style={{width: '350px'}}
            lazy
            onClose={() => history.goBack()}
            hasBackdrop={false}
            canOutsideClickClose={false}
            icon="function"
            isOpen title={"New Service"}
        >
            <NewService/>
        </Dialog>
    );
};
NewServiceDialog.propTypes = {
    history: PropTypes.object.isRequired
};

export const ViewServiceDialog = ({history}) => {
    return (
        <Dialog
            style={{width: '350px'}}
            lazy
            onClose={() => history.goBack()}
            hasBackdrop={false}
            canOutsideClickClose={false}
            icon="function"
            isOpen title={"Service"}
        >
            <ViewService/>
        </Dialog>
    );
};
ViewServiceDialog.propTypes = {
    history: PropTypes.object.isRequired
};


