import * as actionTypes from '../helpers/constants';
import {getTime} from "date-fns";
import MachineServicesApi from '../api/actual/MachineServices';


export const loadServices = (token, params = {}) => dispatch => {
    dispatch({type: actionTypes.LOAD_MACHINE_SERVICES});

    return MachineServicesApi
        .list(token, params)
        .then(({data: services}) => {
            dispatch({type: actionTypes.LOAD_MACHINE_SERVICES_SUCCESSFUL, services});
        })
        .catch(error => {
            dispatch({type: actionTypes.LOAD_MACHINE_SERVICES_FAILED});
            console.log(error);});
};

export const addService = (token, data) => dispatch => {
    dispatch({type: actionTypes.ADD_MACHINE_SERVICE});

    return MachineServicesApi
        .add(token, data)
        .then(({data: {created}}) => {
            if(created){
                dispatch({type: actionTypes.ADD_MACHINE_SERVICE_SUCCESSFUL});
                dispatch({
                    type: actionTypes.OPERATION_SUCCESSFUL,
                    action: actionTypes.ADD_MACHINE_SERVICE,
                    timestamp: getTime(Date()),
                    data: {created}
                });
            }
        })
        .catch(error => {
            dispatch({
                type: actionTypes.OPERATION_FAILED,
                action: actionTypes.ADD_MACHINE_SERVICE,
                timestamp: getTime(Date()),
                data: {...error}
            });
            dispatch({type: actionTypes.ADD_MACHINE_SERVICE_FAILED});
            console.log(error);});
};

export const editService = (token, service, data) => dispatch => {
    dispatch({type: actionTypes.EDIT_MACHINE_SERVICE});

    return MachineServicesApi
        .edit(token, service, data)
        .then(({data: {updated}}) => {
            if(updated){
                dispatch({type: actionTypes.EDIT_MACHINE_SERVICE_SUCCESSFUL});

                dispatch({
                    type: actionTypes.OPERATION_SUCCESSFUL,
                    action: actionTypes.EDIT_MACHINE_SERVICE,
                    timestamp: getTime(Date()),
                    data: {updated}
                });
            }
        })
        .catch(error => {
            dispatch({
                type: actionTypes.OPERATION_FAILED,
                action: actionTypes.EDIT_MACHINE_SERVICE,
                timestamp: getTime(Date()),
                data: {...error}
            });
            dispatch({type: actionTypes.EDIT_MACHINE_SERVICE_FAILED});
            console.log(error);});
};

export const deleteService = (token, service) => dispatch => {
    dispatch({type: actionTypes.DELETE_MACHINE_SERVICE});

    return MachineServicesApi
        .delete(token, service)
        .then(({data: {deleted}}) => {
            if(deleted){
                dispatch({type: actionTypes.DELETE_MACHINE_SERVICE_SUCCESSFUL});
                dispatch({
                    type: actionTypes.OPERATION_SUCCESSFUL,
                    action: actionTypes.DELETE_MACHINE_SERVICE,
                    timestamp: getTime(Date()),
                    data: {deleted}
                });
            }
        })
        .catch(error => {
            dispatch({
                type: actionTypes.OPERATION_FAILED,
                action: actionTypes.DELETE_MACHINE_SERVICE,
                timestamp: getTime(Date()),
                data: {...error}
            });
            dispatch({type: actionTypes.DELETE_MACHINE_SERVICE_FAILED});
            console.log(error);});
};


