import Loadable from 'react-loadable';
import PropTypes from "prop-types";
import {Dialog} from "@blueprintjs/core";
import React from "react";
import Loading from '../../../Common/loading';

const NewApplication = Loadable({
    loader: () => import('./New'),
    loading: () => <Loading/>,
});

const ViewApplication = Loadable({
    loader: () => import('./View'),
    loading: () => <Loading/>,
});


export const NewApplicationDialog = ({history}) => {
    return (
        <Dialog
            backdropClassName="transparent__back"
            style={{width: '350px'}}
            lazy
            onClose={() => history.goBack()}
            hasBackdrop={false}
            canOutsideClickClose={false}
            icon="function"
            isOpen title={"New Application"}
        >
            <NewApplication/>
        </Dialog>
    );
};
NewApplicationDialog.propTypes = {
    history: PropTypes.object.isRequired
};

export const ViewApplicationDialog = ({history}) => {
    return (
        <Dialog
            backdropClassName="transparent__back"
            style={{width: '350px'}}
            lazy
            onClose={() => history.goBack()}
            hasBackdrop={false}
            canOutsideClickClose={false}
            icon="function"
            isOpen title={"Application"}
        >
            <ViewApplication/>
        </Dialog>
    );
};
ViewApplicationDialog.propTypes = {
    history: PropTypes.object.isRequired
};


