import React from 'react';
import PropTypes from 'prop-types';
import { Card, Spinner, Text } from '@blueprintjs/core';
import {NewPrinterDialog, ViewPrinterDialog} from './Modal';
import {Route, Link} from 'react-router-dom';
import {PrintersTable} from './Table';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { bindActionCreators } from 'redux';
import { loadPrinters } from '../../../actions/PrintersActions';


class Printers extends React.Component{

    componentDidMount(){
        const {authUser: {token}, loadPrinters} = this.props;
        loadPrinters(token, {});
    }

    render(){

        return (
            <Card className="page-cell">
                <header className="Header">
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        {this.props.loadingApplications ? <Spinner className="bp3-small"/> : null }
                        <Text className="devices__text">
                            Printers <small className="bp3-text-muted">({this.props.printers.length})</small>
                        </Text>
                    </div>

                    <Link to={`/settings/printers/new`} className="bp3-button bp3-icon-plus"/>
                </header>
                <div className="Content">
                    <Route path="/settings/printers/new" component={NewPrinterDialog}/>
                    <Route path="/settings/printers/:id/view" component={ViewPrinterDialog}/>
                    
                    <PrintersTable data={this.props.printers}/>
                </div>
            </Card>
        );
    }

    static = {
        authUser: PropTypes.object.isRequired,
        printers: PropTypes.array.isRequired,
        loadingPrinters: PropTypes.bool.isRequired,
        loadPrinters: PropTypes.func.isRequired
    };
}

const mapStateToProps = ({authUser, printers, loadingPrinters}) => ({authUser, printers, loadingPrinters});
const mapDispatchToProps = dispatch => bindActionCreators({loadPrinters}, dispatch);

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Printers));


